@import "default-variables";

/**
 * Place your custom styles here.
 */

.main-container {
  background-color: transparent;
  //padding-top: 50px;

  .region-content {

    max-width: 1200px;
    margin: auto;

    .commerce-product {

      display: flex;
      justify-content: space-between;

      .left-side {
        max-width: 60%;

        .product-title h1 {
          font-size: 23px;
          text-transform: uppercase;
          border-bottom: 5px solid $deep-blue;
          padding-bottom: 15px;
          margin: 0;
          color: $deep-blue;
        }

        .retail {
          display: flex;
          justify-content: space-between;
          border-bottom: 5px solid $deep-blue;
          padding: 15px 0;
          align-items: center;

          .field--name-price {
            .field--label {
              display: none;
            }

            font-size: 36px;
            color: $deep-red;
          }

          .add-to-cart {
            .form-group {
              margin-bottom: 0;
            }

            .btn-success {
              background-color: white;
              border: 2px solid $deep-blue;

              border-radius: 0;
              color: $deep-blue;
              text-transform: uppercase;
              font-size: 18px;
            }
          }
        }

        .size-length-color {
          padding: 15px 0;
          display: flex;
          justify-content: space-between;
          border-bottom: 5px solid $deep-blue;

          .field--label {
            color: $deep-red;
            text-transform: uppercase;
            font-size: 16px;
          }

          .color {
            display: flex;
            align-items: center;

            .field--name-field-colr-image {
              padding: 1px;
              border: 1px solid black;
            }

            span {
              padding-left: 5px;
            }
          }
        }

        .highlighted-features {
          display: flex;
        }

        .spec {

          padding-top: 15px;

          .field--label {
            color: $deep-red;
            font-size: 22px;
            margin-bottom: 15px;
          }
        }
      }

      .right-side {
        max-width: 35%;
      }
    }
  }
}
